import "./styles.css";
import React from "react";

export default class App extends React.Component {
  state = {
    date: ""
  };

  componentDidMount() {
    this.getDate();
  }

  getDate = () => {
    var date = new Date().toDateString();
    this.setState({ date });
  };

  render() {
    const { date } = this.state;
    var styles = {
      fontFamily: "sans-serif",
      color: "white",
      backgroundColor: "#3A73AC",
      width: "200px",
      padding: "24px",
      shadowColor: "#000000",
      shadowOffset: "{width: -2, height: 4}",
      shadowOpacity: "0.2",
      shadowRadius: "3"
    };
    return (
      <div>
        <h2 style={styles}>{date}</h2>
      </div>
    );
  }
}
